import { DateTime } from "luxon";

export function convertStringToDate(
  v: string | null | undefined,
): DateTime | null | undefined {
  if (v === undefined || v === null) {
    return v;
  }
  const date = DateTime.fromISO(v, { setZone: true, zone: "utc" }).startOf(
    "day",
  );
  if (date.isValid) {
    return date;
  }
  return null;
}
export function validateDate({
  date,
  constraints,
}: {
  date: DateTime | null;
  constraints: {
    required?: boolean;
    unavailable?: {
      dates?: DateTime[];
      daysOfWeek?: number[];
    };
    minDate?: DateTime;
    maxDate?: DateTime;
  };
}) {
  const { required, unavailable, minDate, maxDate } = constraints;
  if (!date) {
    return required ? false : true;
  }
  if (unavailable?.dates?.some((d) => d.equals(date))) {
    return false;
  }
  if (unavailable?.daysOfWeek?.includes(date.weekday)) {
    return false;
  }
  if (minDate && date < minDate) {
    return false;
  }
  if (maxDate && date > maxDate) {
    return false;
  }
  return true;
}
